<template>
  <div class="header">
    <div class="nav">
      <router-link class="link" to="/">
        <span class="link_name">{{ home }}</span>
      </router-link>
      <router-link class="link" to="/about">
        <span class="link_name">{{ about }}</span>
      </router-link>
      <router-link class="link" to="/contacts">
        <span class="link_name">{{ contacts }}</span>
      </router-link>
      <Switcher></Switcher>
    </div>
  </div>
</template>

<script>
import Switcher from "./LanguageSwitcher";
import { mapState } from "vuex";

export default {
  name: "Header",
  components: {
    Switcher
  },
  computed: {
    ...mapState(["language"]),
    home() {
      return this.language === "English" ? "Home" : "Главная";
    },
    about() {
      return this.language === "English" ? "About" : "Обо мне";
    },
    contacts() {
      return this.language === "English" ? "Contacts" : "Контакты";
    }
  }
};
</script>

<style scoped>
.header {
  width: 100%;
  height: 50px;
  background: #fff;
}

.nav {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.link {
  flex: 1 1 25%;
  width: 25%;
  border: 2px solid rgb(112, 111, 111);
  background: #098609;
  margin: 5px;
  border-radius: 5px;
  text-decoration: none;
  line-height: 30px;
  padding: 5px;
}

.link_name {
  color: #fff;
  font-weight: 600;
}

.link.router-link-exact-active {
  background: #0ba00b;
}

@media (min-width: 320px) and (max-width: 410px) {
  .link_name {
    font-size: 13px;
    font-weight: 400;
    font-size-adjust: inherit;
  }
  .link {
    line-height: 25px;
  }
}
</style>
